import { Button, Grid } from '@mui/material';
import Typography from '@mui/material/Typography';
import { Box } from '@mui/system';
import Carousel from 'react-material-ui-carousel'

import Meta from 'components/Meta';
import { FullSizeFlexBox } from 'components/styled';
import { OnboardProps } from './types';
import { useNavigate } from 'react-router-dom';
import { LayoutSafeArea } from 'components/Layout/LayoutSafeArea';
import { changeIsFirstInstall, selectDeviceInfo } from 'app/app.slice';
import { useAppDispatch, useAppSelector } from 'app/hooks';
import Image from 'mui-image';
import { bridge } from 'app/communicateBridge';
import { jsLog } from 'utils/comunicate';

function Onboard() {
  const dispatch = useAppDispatch()
  const deviceInfo = useAppSelector(selectDeviceInfo)
  const navigate = useNavigate()
  const items = [
    {
      name: 'An toàn, nhanh chóng',
      description: 'Giao dịch được bảo mật an toàn, thao tác đơn giản, nhanh chóng'
    },
  ]

  const Item = ({ item }: OnboardProps) => {
    return (
      <Box>
        <Box sx={{ alignSelf: 'center', mb: 15, alignItems: 'center', display: 'flex', justifyContent: 'center' }}>
          <Image
            src="images/sample.png"
            width={120}
            height={120}
            fit="contain"
            duration={0}
          />
        </Box>
        <Typography sx={{ fontSize: 24, fontWeight: 700, color: '#fff', textAlign: 'center', mb: 1 }}>
          {item?.name}
        </Typography>

        <Typography sx={{ fontSize: 16, color: '#fff', opacity: 0.7, textAlign: 'center' }}>
          {item?.description}
        </Typography>
      </Box>
    )
  }
  return (
    <LayoutSafeArea styles={{ backgroundColor: '#0053FF' }}>
      <Meta title="onBoard" />
      <FullSizeFlexBox
        padding={2.5}
        paddingTop={deviceInfo?.platform?.toUpperCase() === 'IOS' || deviceInfo?.platform?.toUpperCase() === 'ANDROID' ? 0 : 2.5}
        paddingBottom={deviceInfo?.platform?.toUpperCase() === 'IOS' || deviceInfo?.platform?.toUpperCase() === 'ANDROID' ? 0 : 2.5}
        display="flex"
        flexDirection="column"
        justifyContent="space-between"
      >
        <Grid
          container
          direction="column"
          justifyContent="flex-end"
          alignItems="stretch"
          style={{
            height: '100%',
          }}
        >
          <Grid item>
            <Carousel
              autoPlay={false}
              navButtonsAlwaysVisible
              navButtonsAlwaysInvisible
              duration={500}
              indicators={true}
              animation="slide"
              swipe
              fullHeightHover
              activeIndicatorIconButtonProps={{
                style: {
                  backgroundColor: 'white',
                  color: 'white',
                  width: 8,
                  height: 8,
                  marginLeft: 8,
                  marginRight: 8
                }
              }}
              indicatorContainerProps={{
                style: {
                  marginTop: 24
                }
              }}
            >
              {
                items.map((item, i) => <Item key={i} item={item} />)
              }
            </Carousel>
          </Grid>
          <Grid item marginTop={5}>
            <Button fullWidth variant="contained" disableElevation onClick={() => {
              dispatch(changeIsFirstInstall(false))
              bridge.next({
                type: 'jsPreferences',
                data: {
                  isFirstInstall: false
                }
              })
              jsLog({ isFirstInstall: false })
              navigate('/login')
            }}>Bắt đầu</Button>
          </Grid>
        </Grid>
      </FullSizeFlexBox>
    </LayoutSafeArea>
  );
}

export default Onboard;
